import React,{useEffect,useState} from 'react';
import Pagination from '../Pagination/Pagination';


const BlogList = () => {
	const [posts, setPosts] = useState({})
	
	//Fetch Post
	useEffect(() => {
		fetch("/api/v1/posts").then(res => res.json())
			.then(res => setPosts(res))
	}, []);


	
    return ( 

        <>
        <div id="colorlib-container">
			<div className="container">
                






<div className="row row-pb-md">
					
						{posts.data &&  posts.data.map(post => <div className="col-md-4">
						<div className="blog-entry">
							<div className="blog-img">
								<a href="blog.html"><img src="/img/blog-4.jpg" className="img-responsive" alt="html5 bootstrap template"/></a>
							</div>
							<div className="desc">
								<p className="meta">
									<span className="cat"><a href="#">Lifestyle</a></span>
									<span className="date">20 March 2018</span>
									<span className="pos">By <a href="#">Rich</a></span>
								</p>
									<h2><a href="blog.html">{post.title}</a></h2>
								<p>A small river named Duden flows by their place and supplies it with the necessary </p>
							</div>
						</div>
					</div>)}
					
				</div>








                </div></div>


                <Pagination />

                </>
     );
}
 
export default BlogList;