import React,{useState} from 'react';
import db from '../../firebase';
import "./auth.css"

const Login = () => {

const [input,setInput] = useState({email:"",password:""})

const handleSubmit = (e)=>{
e.preventDefault();

db.auth().signInWithEmailAndPassword(input.email,input.password).then(res =>console.log(res)).catch(err => console.error(err))
}

const handleChange = (e)=>{
    setInput({...input, [e.target.name]:e.target.value})
}
    return ( <div id="colorlib-container">
			<div className="container">
                {db.auth().onAuthStateChanged((user)=>{
                    if(user){
                        alert("Welcome User")
                    }
                    else{
                        alert("please login in")
                    }
                })}
                <form className="form" onSubmit={handleSubmit}>
                    
                    <div><input type="email" name="email" onChange={handleChange} placeholder="email"/></div>
                    <div><input type="password" name="password" onChange={handleChange} placeholder="password"/></div>
                    <div><button type="submit">Login</button></div>

                </form>
                </div></div> );
}
 
export default Login;