import React,{useState} from 'react';
import "./dashboard.css"
import CKEditor from "react-ckeditor-component";
import db from '../../firebase';

const Dashboard = () => {
    
const [postDetails,setPostDetails] = useState({title:"",content:""})

const handleChange = e =>{
setPostDetails({...postDetails,[e.target.name]:e.target.value})
}

const handleCkeEditor = e=>{
	setPostDetails({...postDetails,content:e.editor.getData()})
}

const handleSubmit = e=>{
	e.preventDefault();

	

	db.database().ref("bloglist").push({...postDetails}).then(console.log("Post Added")).catch(err=>console.log(err))
}


    return ( <div id="colorlib-container">
			<div className="container">
				<div className="row">
					<div className="col-md-9 content">
						<div className="row row-pb-lg">
							<div className="col-md-12">
								<form onSubmit={handleSubmit}>
									
									<div className="title-input">
										<input type="text" name="title" placeholder="Post Title" onChange={handleChange}/>
										</div>
								<CKEditor 
              activeClass="p10" 
              content={postDetails.content} 
			  events={{"change": handleCkeEditor}}
              
             />
<br/>
			 <button className="btn btn-primary pt-5" type="submit">Create Post</button>
</form>

							</div>
						</div>
				
					</div>
					

<div className="col-md-3">
						<div className="sidebar">
							<div className="side">
                                This is a sidebar
                                </div></div>


				</div>
			</div>
		</div></div> );
}
 
export default Dashboard;

//bloglist = {id:12,title:"post tiet",content:"ehh",comment:[{id}],like:[],dislike:[],author}